import { RouterModule, Routes } from '@angular/router';

import { HomeComponent }  from './home/home.component';
import { AboutComponent } from './about/about.component';

import { AuthGuard }      from './shared';

// TODO: implement these routes
const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'profile', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./+profile/profile.module')['ProfileModule']);  });}) },
  /* { path: 'notloggedin', loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./notloggedin/login.module')['LoginModule']);  });}) }, */
  { path: 'join', canActivate: [AuthGuard], loadChildren: () => new Promise(function (resolve) {  (require as any).ensure([], function (require: any) {    resolve(require('./+join/join.module')['JoinModule']);  });}) },
  { path: 'about', component: AboutComponent}
];

export const routing = RouterModule.forRoot(routes);



